import React from 'react'

export default function xiaogou(props) {
    return (
        <div>
            <button 
            style={{borderRadius:'50%',width:'30px',height:'30px',border:'none',
                    backgroundColor:'#938e8a',color:'#ebeae9'
            }}
            onClick={()=>props.history.push('/home')}>&lt;</button>
            <img 
            src='https://gw.alicdn.com/bao/uploaded/i2/O1CN01k61CH31RSQKWy7bCf_!!0-fleamarket.jpg_790x10000Q75.jpg_.webp'
            style={{height:'100%',width:'100%'}}
            />
            <button style={{height:'30px',width:'40px',backgroundColor:'#ff852a',border:'none',marginLeft:'7%',borderRadius:'5px',marginTop:'60%'}}>店铺</button>
            <button style={{height:'30px',width:'40px',backgroundColor:'#ff852a',border:'none',marginLeft:'5%',borderRadius:'5px'}}>客服</button>
            <button style={{height:'30px',width:'40px',backgroundColor:'#ff852a',border:'none',marginLeft:'5%',borderRadius:'5px'}}>收藏</button>
            <button style={{height:'30px',backgroundColor:'#ff852a',border:'none',marginLeft:'5%',borderRadius:'5px'}}>加入购物车</button>
            <button style={{height:'30px',backgroundColor:'#ff852a',border:'none',marginLeft:'5%',borderRadius:'5px'}}>立即购买</button>
        </div>
    )
}