import React from 'react'
import {Route} from 'react-router-dom'
import {PrivateRoute} from './app'

function Rwr(route) {
    if(route.pri){
      return (
        <PrivateRoute
          path={route.path}
          component={route.component}
          routes={route.routes}
        />
      )
    }
    return (
      <Route
        path={route.path}
        component={route.component}
        routes={route.routes}
        exact={route.exact}
      />
    );
  }

export default Rwr
