import React from 'react'
import { connect } from 'react-redux'
import './Detail.css'

const Detail=(props)=> {
    return (
        <div style={{ display: 'inline-block' }}>
            <div style={{ fontSize: '30px' }} onClick={props.history.goBack}>&lt;</div>
            <div>
                <img src={props.location.idx} />
                <p>{props.location.title}</p>
                {/* console.log({props.location}) */}
                <p>{props.location.price}</p>
            </div>
            <div class='btn' style={{ position: 'absolute', bottom: '0' }}>
                <button class='btn1'>
                    <i className='iconfont icon-icon-test'></i><br />
                        店铺
                    </button>
                <button class='btn1'>
                    <i className='iconfont icon-icon-test3'></i><br />
                        客服
                    </button>

                <button class='btn1'
                // onClick={()=>props.dispatch({
                //     type:'ADD_TODO',
                //     value:`${props.location.title}`
                // })}
                >
                    <i className='iconfont icon-icon-test4'></i><br />
                        收藏
                    </button>
                <button class='btn2'>
                    <i className='iconfont icon-icon-test2'></i><br />
                        加入购物车
                    </button>
                <button class='btn3'>
                    <i className='iconfont icon-icon-test1'></i><br />
                        立即购买
                    </button>
            </div>
        </div>
    )


}

export default connect()(Detail)
