import React from 'react'
import {BrowserRouter as Router,Redirect,Route,Switch} from 'react-router-dom'

import {Provider} from 'react-redux'

import Detail from './Detail'
import Home from './Home'
import Colloect from './Colloect'
import My from './My'
import Shopping from './Shopping'
import More from './More'
import Search from './Search'
import Xiaogou from './xiaogou'
import Tianshi from './tianshi'
import Danshentu from './danshentu'
import Niuquxiaogou from './niuquxiaogou'
import Kuqixiaomao from './kuqixiaomao'

import store from './store'

const App=()=>{
  return <Provider store={store}>
  <Router>
    <Switch>
      <Route exact path='/' component={Home}></Route>
      <Route path='/Colloect' component={Colloect}></Route>
      <Route path='/Detail' component={Detail}></Route>
      <Route path='/Shopping' component={Shopping}></Route>
      <Route path='/My' component={My}></Route>
      <Route path='/More' component={More}></Route>
      <Route path='/Search' component={Search}></Route>
      <Route path='/xiaogou' component={Xiaogou}></Route>
      <Route path='/tianshi' component={Tianshi}></Route>
      <Route path='/danshentu' component={Danshentu}></Route>
      <Route path='/niuquxiaogou' component={Niuquxiaogou}></Route>
      <Route path='/kuqixiaomao' component={Kuqixiaomao}></Route>
      <Route component={Home} />
      {/* <Route render={()=><Redirect to='/' />}/> */}
    </Switch>
    
  </Router>
  </Provider>
}

export default App;

