import React, { Component, useEffect } from 'react'
import './home.css'
import Carouse from './Carouse'
import Tab from './Tab'
import { list } from './actionCreators'
import { connect } from 'react-redux'

const home = (props) => {
    props.dispatch(list())
    
    console.log(props)
    return (
        <div>
            <div className="nav">
                <i style={{ fontsize: 100, color: 'white', fontSize: '20px' }}>淘</i>
                <input
                    type='search'
                    className='Search'
                    onClick={() => props.history.push('/Search')}
                    placeholder='寻找宝贝店铺'
                />
            </div>
            <img
                style={{ width: '100%' }}
                alt=""
            />
            {/* <img src={img} alt="" />*/}
            <Carouse />
            <div>
                {
                    props.getlist.map(f => (
                        <div class='getimg' 
                        onClick={()=>props.history.push({pathname:'/Detail',idx:`${f.img}`,title:`${f.title}`,price:`${f.price}`})}>
                            <img src={f.img} />
                            <p>{f.title}</p>
                            <p>{f.price}</p>
                        </div>
                    ))
                }
            </div>
            <Tab />
        </div>

    )
}
const mapStateToProps = (state) => {
    console.log(state);
    return { getlist: state }
}
export default connect(mapStateToProps)(home);
