import React,{useEffect} from 'react'
import {BrowserRouter as Router,NavLink,Switch,withRouter} from 'react-router-dom'
import Rwr from '../pages/Rwr'

const Doc=(props)=> {
  console.log(props);

  return <Router>
    <ol>
      <li><NavLink activeClassName="active" to='/doc/core'>高级指引</NavLink></li>
      <li><NavLink activeClassName="active" to='/doc/guide'>导航</NavLink></li>
      <li><NavLink activeClassName="active" to='/doc/api'>API</NavLink></li>
      <li><NavLink activeClassName="active" to='/doc/hook'>Hook</NavLink></li>
    </ol>
    <Switch>
      {props.routes.map((route, i) => (
        <Rwr key={i} {...route} />
      ))}
    </Switch>
  </Router>
}

export default withRouter(Doc);
