import React from 'react'
import {connect} from 'react-redux'
import Tab from './Tab'

const Colloect = (props) => {
    //请求数据
    return (
        <div>
            {
                props.data.map((item,idx) => (
                    <li key={{idx}}>
                        <img src={item.img} alt='' />
                        <p>{item.title}</p>
                        <button
                            onClick={()=>{props.dispatch({
                                type:'DEL_TODO',
                                index:idx
                            })}} 
                        >删除</button>
                    </li>
                ))
            }
            <Tab/>
        </div>
    )
}
// const mapStateToProps = (state) => {
//     console.log(state);
//     return {
//         data:state.shoucang
//     }
// }

// export default connect(mapStateToProps)(Colloect)
export default connect()(Colloect)
